<template>
  <div class="share">
    <h1>This is an share page</h1>
    title : {{ title }}
    <br>
    text : {{ text }}
    <br>
    url : {{ url }}
    <br>
    query : {{query }}<br>
    <br>
    params : {{ params }}<br>
    fullPath : {{ fullPath }}<br>
    <br>

    <router-link to="share?title=hello&text=world&url=http%3A%2F%2Fsolid.community%2Fboo">Text One</router-link>

    <router-link to="share?title=O*LA&text=ME&url=http://hello.test">text two</router-link>
    <router-link to="share?title=flik&text=what&url=https://flik.flok@test.gh">TEXT THREE</router-link>
    <!--  <COMPONENET />  -->

    <hr>
    <!-- https://github.com/nico-martin/todo-pwa/blob/master/vue/src/app/Header.vue#L28-L33 -->
    <header class="font-bold text-xl py-3 flex">
      {{ titleS }}
      <button
      v-if="shareFeature"
      class="ml-auto opacity-50 hover:opacity-75"
      @click="openShareDialog()"
      >
      <svg :style="{ width: '1em', height: '1em' }" viewBox="0 0 24 24">
        <path
        fill="currentColor"
        d="M18,16.08C17.24,16.08 16.56,16.38 16.04,16.85L8.91,12.7C8.96,12.47 9,12.24 9,12C9,11.76 8.96,11.53 8.91,11.3L15.96,7.19C16.5,7.69 17.21,8 18,8A3,3 0 0,0 21,5A3,3 0 0,0 18,2A3,3 0 0,0 15,5C15,5.24 15.04,5.47 15.09,5.7L8.04,9.81C7.5,9.31 6.79,9 6,9A3,3 0 0,0 3,12A3,3 0 0,0 6,15C6.79,15 7.5,14.69 8.04,14.19L15.16,18.34C15.11,18.55 15.08,18.77 15.08,19C15.08,20.61 16.39,21.91 18,21.91C19.61,21.91 20.92,20.61 20.92,19A2.92,2.92 0 0,0 18,16.08Z"
        />
      </svg>
    </button>
  </header>

</div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'Share',
  /*  components: {
  'Component': () => import('@/components/Component'),
},*/
data: function () {
  return {
    //  webId: {},
    title:"",
    text:"",
    url: "",
    titleS: 'ToDo PWA (VueJS)',
    shareFeature: 'share' in window.navigator,
    query:  "",
    route: ""
  }
},
created() {
  this.title = this.$route.query.title
  this.text = this.$route.query.text
  this.url = this.$route.query.url
  this.query  = this.$route.query
  this.params = this.$route.params
  this.fullPath =  this.$route.fullPath
  console.log(this.$route)
},
watch: {
  '$route' (to) {
    //  '$route' (to, from) {
    this.title = to.query.title
    this.text = to.query.text
    this.url = to.query.url
    this.query= to.query
    this.params = to.params
    this.fullPath = to.fullPath
    console.log(this.$route.query)
    console.log(to.query)
    //    this.updatePod()
  }
},
methods:{
  /*async updatePod(){
  this.folder = await this.fc.readFolder(this.path)
},*/
openShareDialog: function() {
  window.navigator
  .share({
    title: this.titleS,
    text: 'Share PoPock',
    url: 'https://scenaristeur.github.io/solid-vue-panes/',
  })
  .then(() => console.log('Successful share'))
  .catch(error => console.log('Error sharing', error));
},
}
}
</script>
